* { -webkit-box-sizing: border-box; box-sizing: border-box; }

/*-- body styling  --*/
body {	
    width: 100%; 
    height: 100%;
    margin: 0 auto;
    background-color: white;
    font-family:'Work Sans', sans-serif;
}

h1{
  text-align: center;
  color: #10cabf;
  font-size: 200%;
}

.sort-filter-container{
  width:80%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 auto;
}

.currancy-container{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align: center;
      align-items: center;
  width:80%;
  margin: 0 auto;
}

.currancy-element{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  width:25%;
  height:auto;
  background-color: white;
  border-radius: 0 10px 10px 0;
  border: 1px solid #10cabf;
  border-left: 6px solid #10cabf;
  margin:2px;
}

.currancy-element img{
  -ms-flex-item-align:center;
      align-self:center;
  width:10%;
  height:10%;
  margin: 2px;
}
.nameDiv{
  -ms-flex-item-align:center;
      align-self:center;
  font-size: 100%;
  font-weight: bold;
  color:#15948b;
  margin-bottom: 5px;
}
.currancy-element.span{
  font-size: 100%;
  font-weight: bold;
  color:red;
}
.currancy-element > div {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding:0 10px;

}
.label{
  font-size: 70%;
  font-weight: bold;
  color:#5a6968;
}

input{
  width:100%;
  padding: 12px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #10cabf;
  outline: 0;
  color: #10cabf;
  text-align: center;
  font-size: 200%;
  font-weight: bold;
  -webkit-box-shadow:1px 0px 50px lightgray;
          box-shadow:1px 0px 50px lightgray;
  margin-bottom: 10px;
}

input:hover {
  background-color: #10cabf;
  color: white;
}

button{
  margin:10px;
  border: 1px dotted #10cabf;
  background-color: white;
  color:#10cabf;
  border-radius: 0 10px;
  outline: 0;
}

button:hover{
  border: 1px dotted #5a6968;
  background-color: #10cabf;
  color:white;
}

.currancy-element:hover {
  /* Start the shake animation and make the animation last for 0.5 seconds */
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s; 
  /* When the animation is finished, start again */
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; 
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}

@keyframes shake {
  0% { -webkit-transform: translate(1px, 1px) rotate(0deg); transform: translate(1px, 1px) rotate(0deg); }
  10% { -webkit-transform: translate(-1px, -2px) rotate(-1deg); transform: translate(-1px, -2px) rotate(-1deg); }
  20% { -webkit-transform: translate(-3px, 0px) rotate(1deg); transform: translate(-3px, 0px) rotate(1deg); }
  30% { -webkit-transform: translate(3px, 2px) rotate(0deg); transform: translate(3px, 2px) rotate(0deg); }
  40% { -webkit-transform: translate(1px, -1px) rotate(1deg); transform: translate(1px, -1px) rotate(1deg); }
  50% { -webkit-transform: translate(-1px, 2px) rotate(-1deg); transform: translate(-1px, 2px) rotate(-1deg); }
  60% { -webkit-transform: translate(-3px, 1px) rotate(0deg); transform: translate(-3px, 1px) rotate(0deg); }
  70% { -webkit-transform: translate(3px, 1px) rotate(-1deg); transform: translate(3px, 1px) rotate(-1deg); }
  80% { -webkit-transform: translate(-1px, -1px) rotate(1deg); transform: translate(-1px, -1px) rotate(1deg); }
  90% { -webkit-transform: translate(1px, 2px) rotate(0deg); transform: translate(1px, 2px) rotate(0deg); }
  100% { -webkit-transform: translate(1px, -2px) rotate(-1deg); transform: translate(1px, -2px) rotate(-1deg); }
}


